.media {
  display: inline-block;
}

.mask {
  position: fixed;
  z-index: 999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.6);
}

.urlInputContainer {
  background-color: #fff;
  width: 300px;
  padding: 10px 20px;
}

.checkboxInput {
  font-size: 14px;
}

.nameInput,
.urlInput {
  display: block;
  line-height: 32px;
  width: 100%;
  margin: 10px 0;
  padding: 0 5px;
}

.audo {
  max-width: 200px;
}

.limitSize {
  max-width: 600px;
  margin: 0 auto;
}
.iframeContainer {
  width: 100%;
  height: 0;
  position: relative;
  padding-bottom: 56.25%;
}

.iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.image {
  max-width: 100%;
  height: auto;
}

.btnLine {
  text-align: center;
  button {
    box-shadow: none;
    padding: 0 20px;
    line-height: 30px;
    font-size: 14px;
    margin: 0 10px;
    background-color: #fff;
    color: #333;
    box-sizing: border-box;
    border: 1px solid #ddd;
    cursor: pointer;
  }
}



