figure {
  margin: 0 1em;
}
.editor {
  position: relative;
  margin: 0;
  padding: 0;
}
.editor .content {
  padding: 8px;
  min-height: 100px;
}
.editor.hidePlaceholder .public-DraftEditorPlaceholder-root {
  display: none;
}
.editor.mobile {
  background: url("//i.ezbuy.sg/FpjrbpcApGlAaI1oXJLLnvMzLEop") no-repeat top center;
  background-size: 436px auto;
  padding: 54px 0 28px;
  box-sizing: border-box;
}
.editor.mobile .inner {
  width: 375px;
  height: 788px;
  border-radius: 0 0 30px 30px;
  max-height: 100%;
  margin: 0 auto;
  overflow-y: auto;
  background: #fff;
}
.blockquote {
  border-left: 5px solid #eee;
  color: #666;
  margin: 16px 0;
  padding: 10px 20px;
}
.btnPreview {
  position: absolute;
  z-index: 100;
  right: 0;
  top: 0;
}
.center :global .public-DraftStyleDefault-block {
  text-align: center;
}
.right :global .public-DraftStyleDefault-block {
  text-align: right;
}



